* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.carditem {
  width: 90%;
  display: flex;
  flex-direction: column;
  background-color: #8989e6;
  margin: 1rem;
  border-radius: 10px;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.3);
}

h3, p {
  margin: 5px 10px;
  color: #000049;
}